.person-card {
    display: flex;
    flex-direction: row;
    padding-left: 20px;
    padding-right: 20px;
    border-style: solid;
    border-width: 0.1px;
    border-color: #dddddd;
}

.person-card:hover {
    background-color: #eeeeee;
}

.person-portrait {
    order: 0;
    align-self: center;
}

.person-profile {
    order: 1;
    padding: 10px;
}

.name {
    font-weight: 500;
}

.description {
    font-weight: 300;
}